body {
  padding-top: 40px; }

/* ENV INDICATOR */
.env {
  margin-top: -4px;
  padding-top: 4px;
  position: fixed; }

.env .label {
  padding-top: 4px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 0; }

/* KABOOMBAR */
.kb-navbar {
  z-index: 1000;
  min-height: 40px; }

.kb-navbar .active {
  position: relative; }

.kb-navbar .active:after {
  content: '';
  position: absolute;
  bottom: -5px;
  border-bottom: 4px solid #08c;
  display: block;
  width: 100%;
  z-index: 99999; }

.kb-navbar .navbar-brand {
  padding: 9px 10px; }

.kb-navbar.shadow {
  box-shadow: 0 2px 2px 2px rgba(51, 51, 51, 0.3); }

.kb-navbar .navbar-nav > li > a {
  padding-top: 10px;
  padding-bottom: 10px; }

.kb-cols {
  padding-left: 20px;
  padding-right: 20px; }

.kb-navbar .navbar-collapse {
  max-height: 600px; }

/* KABOOM */
.kaboom {
  box-shadow: 0 2px 2px 2px rgba(51, 51, 51, 0.3); }

.kaboom .dark .nav-header {
  color: black; }

.kaboom .dark .nav-header-dark {
  background-color: #7A7A7A;
  color: white;
  text-shadow: #888 0px 1px 0px; }

.kaboom .nav-list {
  padding-bottom: 20px; }

.kaboom .nav-list li > a:hover {
  background: none;
  text-decoration: underline; }

.shadow .nav .always-active > a {
  color: #ffffff !important;
  background-color: #111111; }

.nav .always-active > a {
  color: #999 !important;
  background-color: #111111; }

.nav .always-active.active > a {
  color: #ffffff !important;
  background-color: #111111; }

.kaboom {
  background-color: #f2f2f2;
  display: none;
  position: fixed;
  z-index: 9000;
  width: 100%; }

.kaboom .news-and-stats {
  padding-top: 33px; }

.kaboom .landing-row {
  padding-top: 15px;
  padding-bottom: 15px; }

.kb-navbar > .navbar-collapse {
  overflow-x: hidden; }

@media print {
  .navbar {
    display: none !important; } }

@media (max-width: 992px) {
  .kb-navbar {
    position: relative; }
  /* override Bootstrap here as the padding is undesirable */
  .navbar-fixed-top .navbar-nav {
    padding: 0px !important; }
  /* Bootstrap reverts navbar to static positioning for this media query, so do the same for kabooms */
  .kaboom {
    position: absolute; }
  /* Don't need the padding anymore as the navbar is statically positioned */
  body {
    padding-top: 0; }
  /* Bootstrap adds a margin below the navbar, override this */
  .navbar-fixed-top {
    margin-bottom: 0 !important; } }

@media (min-width: 768px) {
  .kb-cols {
    padding-top: 33px; }
  .kaboom .landing-row {
    padding-top: 33px;
    padding-bottom: 20px; }
  .kaboom > .container > .row {
    margin-left: 15px;
    margin-right: 15px; }
  .kb-navbar > .navbar-header {
    float: right; }
  .kaboom .dark .nav-header-dark {
    text-align: right; }
  .kaboom .row {
    overflow: hidden; }
  .kaboom .col-sm-1,
  .kaboom .col-sm-2,
  .kaboom .col-sm-3,
  .kaboom .col-sm-4,
  .kaboom .col-sm-5,
  .kaboom .col-sm-6,
  .kaboom .col-sm-7,
  .kaboom .col-sm-8,
  .kaboom .col-sm-9,
  .kaboom .col-sm-10,
  .kaboom .col-sm-11,
  .kaboom .col-sm-12 {
    margin-bottom: -99999px;
    padding-bottom: 99999px; } }

.kaboom div.dark {
  box-shadow: inset 0 10px 10px rgba(34, 34, 34, 0.3);
  background-color: #ccc; }

.kaboom .dark .nav-list li.dark > a {
  color: #333333; }

/* NAVLIST */
.nav-list {
  margin-bottom: 0; }

.nav li + .nav-header {
  margin-top: 9px; }

.nav-list > li > a,
.nav-list .nav-header {
  margin-right: -15px;
  margin-left: -15px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5); }

.nav-list > li > a {
  padding: 3px 15px; }

.nav-list > .active > a,
.nav-list > .active > a:hover {
  color: #ffffff;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);
  background-color: #0088cc; }

.nav-list [class^="icon-"] {
  margin-right: 2px; }

.nav-list .divider {
  *width: 100%;
  height: 1px;
  margin: 9px 1px;
  *margin: -5px 0 5px;
  overflow: hidden;
  background-color: #e5e5e5;
  border-bottom: 1px solid #ffffff; }

.nav-list .nav-header {
  padding: 3px 15px;
  font-size: 11px;
  font-weight: bold;
  line-height: 20px;
  color: #999;
  text-transform: uppercase; }

/* SALES STATISTICS */
.statistics caption {
  text-align: left;
  color: #999999;
  text-transform: uppercase;
  font-size: 11px;
  font-weight: bold; }

.statistics thead tr th:nth-child(n+2),
.statistics .table tbody > tr > td:first-child {
  color: #999999; }

.statistics .table tbody > tr > td:nth-child(n+2) {
  font-weight: bold; }

.statistics .over-target {
  color: #4CA58A; }

.statistics .under-target {
  color: #DF505C; }

/* NEWS */
.news .news-item a {
  color: black;
  font-weight: bold; }

.news .news-item .label {
  margin-left: 10px;
  font-size: 10px;
  text-transform: uppercase; }

.news .news-item .label-important {
  background-color: #797979; }

.news .news-item .label-vacancy {
  background-color: #48A187; }

/* TICKER */
.ticker {
  margin-top: 10px;
  white-space: nowrap;
  font-size: 13px;
  height: 28px; }

.ticker .col-xs-10 {
  height: 28px;
  overflow: hidden;
  text-overflow: ellipsis; }

.ticker-notification,
.ticker-notification:hover {
  display: inline;
  color: #000000; }

.ticker-notification .label-important {
  background-color: #797979; }

.ticker-notification .label-vacancy {
  background-color: #48A187; }

.ticker-notification .notification-title,
.ticker-notification .label {
  margin-right: 16px; }

.ticker-notification .notification-title {
  font-weight: bold; }

.ticker-notification .notification-content {
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%; }

.kb-navbar .glyphicon-info-sign {
  font-size: 18px; }

.kb-navbar a[data-toggle=kaboom] {
  height: 40px; }
